import React from 'react'
import Link from 'gatsby-link'

import logo from '../assets/images/JoystickV2.png'

const Header = props => (
  <header id="header" className="alt">
    <span className="logo">
      <img src={logo} />
    </span>

    <p />
  </header>
)

export default Header



